import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { AdaptivityProvider, AppRoot, FixedLayout, PromoBanner } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';

import Home from './panels/Home';
import Persik from './panels/Persik';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const share = e => {
		bridge.send("VKWebAppShare", {"link": "https://vk.com/app7799944"})
	}


	const promoBannerProps = {
		title: 'Берроуз Бот',
		domain: 'Самый многофункциональный бот ВКонтакте!',
		trackingLink: 'https://vk.com/burroughs_bot',
		ctaText: 'Перейти',
		advertisingLabel: 'Рекомендация',
		iconLink: 'https://sun9-42.userapi.com/impg/9WTL_V43MpBv-zoHhSSuSxdVbQZS6xg_bixFew/XUFKrQJIF6k.jpg?size=200x200&quality=96&sign=77f70305d4d0f84f994e810938c6fd9d&type=album',
		description: 'Самый многофункциональный бот!',
		ageRestrictions: "0",
		statistics: [
		  { url: '', type: 'playbackStarted' },
		  { url: '', type: 'click' }
		]
	  };
	
	const advert = e => {
	bridge.send('VKWebAppGetAds')
    .then((promoBannerProps) => {
        this.setState({ promoBannerProps });
    })
	{ this.state.promoBannerProps && <PromoBanner bannerData={ this.state.promoBannerProps } /> }
	}

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel}  popout={popout}>
					<Home id='home' fetchedUser={fetchedUser} share={share} />
				</View>
				{/* <View activePanel="promo">
				<Panel id="promo">
					<FixedLayout vertical="bottom">
					<PromoBanner bannerData={promoBannerProps} />
					</FixedLayout>
				</Panel>
				</View> */}
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;

